import React, { useEffect, useRef, useState } from "react";
import { useCart, useDispatch } from "./ContextReducer";

function Card(props) {
  let options = props.options;
  let priceOption = Object.keys(options);
  let priceRef = useRef();
  const [qty, setQty] = useState(1);
  const [size, setSize] = useState("");
  let dispatch = useDispatch();
  let data = useCart();
  const handleAddtoCart = async () => {
    let food = [];
    for (const item of data) {
      if (item.id === props.foodItem._id) {
        food = item;
        break;
      }
    }
    if (food !== []) {
      if (food.size === size) {
        await dispatch({
          type: "UPDATE",
          id: props.foodItem._id,
          price: finalPrice,
          qty: qty,
        });
        return;
      } else if (food.size !== size) {
        await dispatch({
          type: "ADD",
          id: props.foodItem._id,
          name: props.foodItem.name,
          price: finalPrice,
          qty: qty,
          size: size,
        });
        return;
      }
      return;
    }
    await dispatch({
      type: "ADD",
      id: props.foodItem._id,
      name: props.foodItem.name,
      price: finalPrice,
      qty: qty,
      size: size,
    });

    // console.log(data);
  };
  let finalPrice = qty * parseInt(options[size]);
  useEffect(() => {
    setSize(priceRef.current.value);
  }, []);

  return (
    <div>
      <div
        className="card mt-3 mx-3 mb-2"
        style={{ width: "18rem", maxHeight: "410px" }}
      >
        <img
          className="card-img-top"
          src={props.foodItem.img}
          alt="Card image cap"
          style={{ height: "150px", objectFit: "fill" }}
        />
        <div className="card-body text-light">
          <h3 className="card-title">{props.foodItem.name}</h3>

          <div className="container w-100">
            <select
              className="m-2 h-100 bg-success rounded"
              onChange={(e) => {
                setQty(e.target.value);
              }}
            >
              {Array.from(Array(6), (e, i) => {
                return (
                  <option key={i + 1} value={i + 1}>
                    {i + 1}
                  </option>
                );
              })}
            </select>
            <select
              className="m-2 h-100 bg-success rounded"
              ref={priceRef}
              onChange={(e) => {
                setSize(e.target.value);
              }}
            >
              {priceOption.map((data) => {
                return (
                  <option key={data} value={data}>
                    {data}
                  </option>
                );
              })}
              ;
            </select>
            <div className="d-inline text-light h-100 fs-5">
              Rs.{finalPrice}/-
            </div>
          </div>
        </div>
        <hr />
        <button
          className="btn btn-success text-white w-50 mx-auto mb-3"
          onClick={handleAddtoCart}
        >
          Add to Cart
        </button>
      </div>
    </div>
  );
}

export default Card;
