import React, { createContext, useContext, useReducer } from "react";

const CartstateContext = createContext(); ///making it as globle state that can be changed from anywhere
const CartDispatchContext = createContext(); ///multiple actions are in dispatch like add,remove etc like set state
const reducer = (state, actions) => {
  /// all the logic is in this add to cart delete etc
  switch (actions.type) {
    case "ADD":
      return [
        ...state,
        {
          id: actions.id,
          name: actions.name,
          qty: actions.qty,
          size: actions.size,
          price: actions.price,
          img: actions.img,
        },
      ]; ///state will remain same and we append data
    case "REMOVE":
      let newState = [...state];
      newState.splice(actions.index, 1);
      return newState;
    case "UPDATE":
      let arr = [...state];
      arr.find((food, index) => {
        if (food.id === actions.id) {
          arr[index] = {
            ...food,
            qty: parseInt(actions.qty) + food.qty,
            price: actions.price + food.price,
          };
        }
        return arr;
      });
      return arr;
    case "DROP":
      let emptyArray = [];
      return emptyArray;
    default:
      console.log("error in reducer...");
  }
};
export const CartProvider = ({ children }) => {
  const [state, dispatch] = useReducer(reducer, []); ///initial value of state and dispatch function
  return (
    <CartDispatchContext.Provider value={dispatch}>
      <CartstateContext.Provider value={state}>
        {children}
      </CartstateContext.Provider>
    </CartDispatchContext.Provider>
  );
};
export const useCart = () => useContext(CartstateContext);
export const useDispatch = () => useContext(CartDispatchContext);
